<template>
  <div>
    <el-container >
    <el-header>
      <el-cascader
        :options="typedata"
        :props="defaultProps"
        @change="(val) => {if(!value){ delete searchItem.passage1;}else{searchItem.passage1 = val[val.length-1]};initMainData();}"
        placeholder="运动项目"
        change-on-select
        clearable
        >
      </el-cascader>
      <el-cascader
        :options="areadata"
        :props="defaultProps"
        @change="(val) => {if(!value){ delete searchItem.passage2;}else{searchItem.passage2 = val[val.length-1]};initMainData();}"
        placeholder="区域类别"
        change-on-select
        clearable
        >
      </el-cascader>

      <el-select 
        v-model="searchForm.type"  
        @change="(value) => { 
          if(!value){ delete searchItem.type;}else{searchItem.type = value;};initMainData();
        }" 
        placeholder="请选择商户类别" 
        clearable 
      >
          <el-option  label="健身馆" value="1" ></el-option>
          <el-option  label="健身餐" value="2" ></el-option>

      </el-select>

      <el-button type="primary" @click="handleAdd()">创建商户</el-button>
    </el-header>
    <el-main style="height: 700px; border: 2px solid #eee">
      <el-table :data="mainData" style="width: 100%"> 
        
        <el-table-column label="商户ID" prop="id">
        </el-table-column>
        <el-table-column label="商户名称" prop="name">
        </el-table-column>
        <el-table-column label="描述" prop="description">
        </el-table-column>
        <el-table-column label="手机号" prop="phone">
        </el-table-column>
        <el-table-column
          label="店铺主图"
          width="180">
          <template slot-scope="scope">
            <div v-if="scope.row.mainImg" >
              <div v-for="item in scope.row.mainImg" style="display: inline-block;">
                <img :src="item.url"  style="width: 60px;">
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="权限参数" prop="primary_scope">
        </el-table-column>
        <el-table-column
          label="创建时间"
          prop="create_time">
        </el-table-column>
        <el-table-column label="操作" width="250">
          <template scope="scope">
            <el-button
              size="small"
              @click="handleEdit(scope.row)">
              编辑
            </el-button>

            <el-button
            size="small"
            v-if="userKey == scope.row.name" @click="handleEditPassword(scope.row)">变更密码
            </el-button>

            <el-button
            size="small"
            @click="handleDel(scope.row)">
            删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog  title="项目信息" :visible.sync="dialogFormVisible"  id="dialog">
        <el-form :model="Form" ref="Form" >
          <div v-if="dialogType=='two'">
            <el-form-item label="用户密码" :label-width="formLabelWidth" prop="password">
              <el-input v-model="Form.password" @change="sForm.password = arguments[0]">
              </el-input>
            </el-form-item>
          </div>
          <div v-else>
            <el-form-item label="商户类别" :label-width="formLabelWidth">
              <el-select 
                v-model="Form.type"   
                @change="sForm.stype = Form.type" 
                placeholder="请选择商户类别" 
                clearable 
              >
                  <el-option value="1" label="健身馆" ></el-option>
                  <el-option value="2" label="健身餐" ></el-option>
              </el-select>
            </el-form-item>  
            <el-form-item label="运动项目分类" :label-width="formLabelWidth">
              <el-cascader
              :options="typedata"
              :props="defaultProps"
              v-model="Form.type_array"
              @change="(val)=>{sForm.passage1 = val[val.length-1];}"
              change-on-select
              clearable>
              </el-cascader>
            </el-form-item>
            <el-form-item label="区域类别" :label-width="formLabelWidth">
              <el-cascader
              :options="areadata"
              :props="defaultProps"
              v-model="Form.area_array"
              @change="(val)=>{sForm.passage2 = val[val.length-1];}"
              change-on-select
              clearable>
              </el-cascader>
            </el-form-item>

            <el-form-item label="用户名" :label-width="formLabelWidth" prop="name">
            <el-input v-model="Form.name" @input="sForm.name = arguments[0]">
            </el-input>
            </el-form-item>
            <el-form-item label="描述" :label-width="formLabelWidth" prop="description">
              <el-input v-model="Form.description" @input="sForm.description = arguments[0]" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="排序" :label-width="formLabelWidth" prop="listorder">
              <el-input v-model="Form.listorder" @input="sForm.listorder = arguments[0]" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="手机号" :label-width="formLabelWidth" prop="phone">
              <el-input v-model="Form.phone"  @change="sForm.phone = Form.phone" auto-complete="off"></el-input>
            </el-form-item>                    
            <el-form-item label="店铺主图" prop="mainImg">
            <imgup @imgchange ="(val)=>{imgchange(val,'sForm','mainImg')}" :imglist = "Form.mainImg" :imglimit = "imglimit"></imgup>
            </el-form-item>
            <el-form-item label="轮播图" prop="bannerImg">
            <imgup @imgchange ="(val)=>{imgchange(val,'sForm','bannerImg')}" :imglist = "Form.bannerImg" :imglimit = "imglimit"></imgup>
            </el-form-item>
            <el-form-item label="图文编辑" >
             <editor @contentsave = "(val)=>{sForm.content = val;Form.content = val;}" 
             :defaultcontent = "Form.content">
             </editor>
            </el-form-item>

          </div>
          

          <el-button type="primary" @click="onSubmit()">确 定</el-button>
          
        </el-form>              
      </el-dialog>

    </el-main>
    <el-footer>
      <el-pagination
        @size-change="(val) => {paginate.pagesize = val;initMainData();}"
        @current-change="(val) => {paginate.currentPage = val;initMainData();}"
        :current-page="paginate.currentPage"
        :page-sizes="[10, 50, 70, 100]"
        :page-size="paginate.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="paginate.count">
      </el-pagination>
    </el-footer>
  </el-container>

  </div>
  

  
  
</template>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>

<script>
  
  import editor from '../common/VueEditor'; 
  import imgup from '../common/UploadImg'; 
  import {merchantList,merchantEdit,merchantAdd,merchantDel,uptPass,getMenutree} from '@/api/getData';
  import {notify,sCallBack,cloneForm,formatDate,imgchange,getParentNames} from '@/api/commonFunc';
  
  
   
  export default {

    data() {
      return {
        mainData: [],//承载主列表数据
        typedata:[],
        areadata:[],
        dialogFormVisible: false,//dialog的显示参数，默认不显示
        formLabelWidth: '120px',//设置的el-form-item的宽度
        paginate: {
            count: 0,
            currentPage: 1,
            pagesize:10,
            is_page:true,
        },//分页的基础设置参数
        searchItem:{},
        searchForm:{
          type:''
        },       
        imglimit:2,//设置上传插件允许上传的张数      
        Form: {},//承载页面数据装载
        standardForm:{
          name: '',
          appid: '',
          appsecret: '',
          app_description: '',
          scope: '',
          scope_description: '',
          app_type: '',
          img: [],
          phone: '',
          picstandard: '',
          mchid: '',
          wxkey: '',
          password: '',

        },
        sForm: {},//承载提交数据装载
        passForm:{
          password:'',
        },
        dialogType:'one',
        userKey:this.$store.getters.getUserInfo.username,
        token:this.$store.getters.getUserInfo.token,
        thirdapp_id:this.$store.getters.getUserInfo.thirdapp_id,
        defaultProps: {
          children: 'child',
          label: 'name',
          value: 'id',
        },

      }
    },
    created(){
      this.initMenuData();
      this.initMainData();
    },
    components: {
      editor,
      imgup,
    },
    methods: {


      async initMenuData(){
        const self =this;
        const postData = {};
        postData.token = self.token;
        postData.searchItem = {
          parentid:7,
        };

        try{
          var res = await getMenutree(postData);
        }catch(err){
          console.log(err); 
          notify('网络故障','error');
        };
         
        if(res){
          self.typedata = res.data[0].child;
          self.areadata = res.data[1].child;
          console.log(self.typedata)
        };

        
      },


      //页面数据初始化
      async initMainData(){
        const self = this;
        self.mainData = [];
        const postData  = self.paginate;        
        postData.token = self.token; 
        postData.searchItem = cloneForm(self.searchItem); 
        try{
          var res = await merchantList(postData);
        }catch(err){
          console.log(err); 
          notify('网络故障','error');
        }; 
        if(res){
          self.paginate['count'] = res.data.total;
          self.mainData = res.data.data;
        };
      },


      //add按钮触发事件
      handleAdd(){
        const self = this;
        self.dialogType = 'one';
        self.Form = cloneForm(self.standardForm);
        self.sForm = {};
        self.sForm.type = 'add';
        self.sForm.token = self.token; 
        self.dialogFormVisible = true;
      },


      //edit按钮触发事件
      handleEdit(row) {
        const self = this;
        self.dialogType = 'one';
        self.Form = cloneForm(row);
        self.Form['type_array'] = getParentNames(self.typedata,parseInt(self.Form.passage1)).reverse();
        console.log(self.Form['type_array']) 
        self.Form['area_array'] = getParentNames(self.areadata,parseInt(self.Form.passage2)).reverse();
        self.Form.type = self.Form.type.toString();
        console.log() 
        self.sForm = {};
        self.sForm.id = row.id;
        self.sForm.token = self.token;  
        self.sForm.type = 'edit';
        self.dialogFormVisible = true;
      },


      //delete按钮触发事件
      handleDel(row){
        const self = this;
        self.sForm = {};
        self.sForm.id = row.id;
        self.sForm.token = self.token;
        self.sForm.type = 'del';
        self.onSubmit();    
      },

      //变更密码
      handleEditPassword(row){
        const self = this;
        self.dialogType = 'two';
        self.sForm = {};
        self.Form = {};
        self.Form.password = "";
        self.sForm.id = row.id;
        self.sForm.token = self.token;
        self.sForm.type = 'upPassword';
        self.dialogFormVisible = true;
      },


      //dialog中提交按钮事件
      async onSubmit() {

        const self = this;
        console.log(self.sForm);
        if (self.sForm.type == 'edit') {

          delete self.sForm.type;
          self.sForm.type = self.sForm.stype;
          try {
            var res = await merchantEdit(self.sForm); 
          } catch (err) {
              console.log(err); 
              notify('网络故障','error'); 
          };
          self.sForm.type = 'edit';
                        
        }else if(self.sForm.type == 'add'){
        
          delete self.sForm.type;
          self.sForm.type = self.sForm.stype;
          try {
            var res = await merchantAdd(self.sForm); 
          } catch (err) {
              console.log(err); 
              notify('网络故障','error');              
          };
          self.sForm.type = 'add';

                                                              
        }else if(self.sForm.type == 'upPassword'){

          delete self.sForm.type;
          try {
            var res = await uptPass(self.sForm); 
  
          } catch (err) {
              console.log(err); 
              notify('网络故障','error');
          };
          self.sForm.type = 'upPassword';
          
        }else if(self.sForm.type == 'del'){
       
          delete self.sForm.type;
          try{
            var res = await merchantDel(self.sForm);
          }catch(err){
            console.log(err);
            notify('网络故障','error');
          };
          self.sForm.type = 'del';

        };

        

        if(res){
          var fCall =  sCallBack(res);
          if(fCall == 'success'){
            self.dialogFormVisible = false;
            self.initMainData();
          };
        };
        

      },
     

      //图片上传的回调
      imgchange(data,formName,fieldName){
          return imgchange(this,data,formName,fieldName);
      },
      

    }
  }
</script>


